.activeLine {
    background-color: $mine-shaft;
}
.nav {
    background-color: $white;
    position: fixed;
    width: 100%;
    height: 70pt;
    z-index: 9999;

    @media only screen and (max-width: 576pt) {
        height: auto;
    }

    &__logo {
        font-family: 'Roboto', sans-serif;
        font-size: 2rem;
        user-select: none;
        padding: 20pt;
        float: left;

        @media only screen and (max-width: 576pt) {
            float: left;
        }

        &--first-character {
            transform: rotate(90deg);
            float: left;
        }

        &--second-character {
            float: left;
        }
    }

    .show {
        display: block;
    }

    .hide {
        display: none;;
    }

    &__burger {
        position: absolute;
        background-color: $mine-shaft;
        width: 35pt;
        height: auto;
        top: 20pt;
        right: 25pt;
        padding: 7pt;
        border-radius: 5pt;
        cursor: pointer;

        @media only screen and (max-width: 576pt) {
            display: block;
        }

        & > span {
            background-color: $white;
            width: 100%;
            height: 2.5pt;
            border-radius: 10pt;
            float: left;

            &:first-child {
                margin-bottom: 5pt;
            }

            &:last-child {
                margin-top: 5pt;
            }
        }
    }

    &__list {
        position: absolute;
        width: 400pt;
        margin: 0;
        list-style: none;
        list-style-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
        top: 50%;
        right: 0;
        transform: translate(0, -50%);

        @media only screen and (max-width: 576pt) {
            position: relative;
            width: 100%;
            padding: 0;
            margin: 0;
            top: 0;
            transform: translate(0, 0);
            float: left;
        }

        &--item {
            width: auto;
            padding: 5pt 10pt;
            margin: 0 5pt;
            float: left;
            transition: 150ms;
            cursor: pointer;

            @media only screen and (max-width: 576pt) {
                width: 100%;
                padding: 10pt;
                margin: 0;
            }

            &:hover {
                background-color: $mine-shaft;
                color: $white;
                transition: 150ms;
            }
        }

        .active {
            background-color: $mine-shaft;
            color: $white;
            transition: 150ms;
        }
    }
}