.main {
    position: relative;
    background-color: $white;
    width: 100%;
    height: auto;
    overflow: auto;
    padding: 70pt 0 0 0;
}

.container {
    display: inline-block;
    width: 100%;
    height: auto;
    overflow: hidden;
    
    &__half {
        display: flex;
        width: 50%;
        height: 350pt;
        padding: 30pt;
        flex-wrap: wrap;
        align-content: center;
        justify-content: center;
        float: left;

        @media only screen and (max-width: 576pt) {
            width: 100%;
            height: auto;
            float: left;
        }

        &--content {
            flex: 1;

            @media only screen and (max-width: 576pt) {
                overflow: hidden;
                float: left;
            }
        

            h1 {
                font-size: 1.5rem;
                text-transform: uppercase;
                text-align: center;
            }

            p {
                font-size: 1.25rem;
                text-align: center;
                line-height: 2;
            }
        }

        &--svg {
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
            width: 100pt;
            height: 100pt;
            flex: 1;
        }

        &--contact {
            width: 100%;
            height: auto;
            margin: 5pt 0;
            float: left;

            &__icon {
                background-position: center center;
                background-repeat: no-repeat;
                background-size: contain;
                width: 25pt;
                height: 25pt;
                margin: 0 15pt 0 0;
                float: left;
            }

            &__text {
                line-height: 25pt;
                font-size: 1.25rem;
                float: left;

                @media only screen and (max-width: 576pt) {
                    font-size: 1rem;
                }
            }
        }
    }

    &__information {
        position: relative;
        background-image: radial-gradient(circle, $white, $alto);
        display: inline-block;
        width: 100%;
        height: 400pt;
        padding: 200pt 100pt;
        box-shadow: 0 7.5pt 10pt -5pt $silver;
        overflow: hidden;

        @media only screen and (max-width: 576pt) {
            font-size: 1.5rem;
            padding: calc(200pt) 30pt;
        }

        @media only screen and (max-width: 350pt) {
            padding: calc(100pt + 40pt) 10pt;
        }

        .curve-top {
            position: absolute;
            background-color: $white;
            width: 100%;
            height: 100pt;
            top: 0;
            right: 0;
            left: 0;
            border-radius: 50%/0 0 30pt 30pt;
            box-shadow: 0 7.5pt 10pt -5pt $silver;
        }

        &--text {
            color: $mine-shaft;
            font-size: 1.5rem;
            line-height: 2;
            text-align: center;
            user-select: none;

            @media only screen and (max-width: 576pt) {
                font-size: 1rem;
            }
        }
    }

    &__project {
        display: flex;
        width: 100%;
        height: auto;
        margin: 20pt 0;
        flex-wrap: wrap;
        justify-content: center;
        align-content: center;
        float: left;

        @media only screen and (max-width: 576pt) {
            height: auto;
        }

        &--content {
            max-width: 350pt;
            min-height: 350pt;
            padding: 20pt;
            flex: 1;
            box-shadow: 0 0 5pt 3pt $silver;

            @media only screen and (max-width: 576pt) {
                width: 100%;
                max-width: 100%;
            }
            
            &__image {
                background-position: center center;
                background-repeat: no-repeat;
                background-size: contain;
                width: 100%;
                height: 225pt;
                margin: 0 0 10pt 0;
                float: left;
            }
            
            &__title {
                width: 100%;
                font-size: 1.5rem;
                text-transform: uppercase;
                text-align: center;
                float: left;
            }

            &__description {
                width: 100%;
                text-align: center;
                margin: 10pt 0;
                float: left;
            }

            &__tags {
                display: flex;
                width: 100%;
                flex-wrap: wrap;
                justify-content: center;
                align-content: center;
                float: left;

                & > span {
                    width: auto;
                    background-color: $rock-blue;
                    font-size: 0.75rem;
                    padding: 5pt 10pt;
                    margin: 0 5pt;
                    vertical-align: baseline;
                    border-radius: 15pt;
                    float: left;

                    @media only screen and (max-width: 576pt) {
                        margin: 5pt;
                    }
                }
            }
        }
    }
}
