.header {
    position: relative;
    background-image: radial-gradient(circle, $white, $gallery);
    width: 100%;
    height: calc(100vh - 70pt);
    overflow: hidden;

    &__title {
        position: absolute;
        font-family: 'Roboto', sans-serif;
        font-size: 2rem;
        font-weight: 700;
        text-transform: uppercase;
        top: 50%;
        left: 50pt;
        transform: translate(0, -50%);
        user-select: none;

        @media only screen and (max-width: 576pt) {
            font-size: 2rem;
            left: 10pt;
            top: 30%;
        }

        @media only screen and (max-width: 400pt) {
            left: 30pt;
            font-size: 1.5rem;
        }

        @media only screen and (max-width: 350pt) {
            left: 10pt;
        }

        @media only screen and (max-width: 325pt) {
            left: 30pt;
            font-size: 1rem;
        }

        @media only screen and (max-width: 275pt) {
            left: 10pt;
        }

        &--subtitle {
            font-size: 1rem;
            font-style: italic;
            font-weight: 200;
            font-family: 'Montserrat', sans-serif;
            text-transform: none;
            text-align: center;

            @media only screen and (max-width: 576pt) {
                font-size: 1rem;
            }

            @media only screen and (max-width: 400pt) {
                font-size: 0.75rem;
            }
        }
    }

    &__cover {
        position: absolute;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        width: 400pt;
        height: 600pt;
        top: 0;
        right: 30pt;

        @media only screen and (max-width: 750pt) {
            right: -100pt;
        }

        @media only screen and (max-width: 576pt) {
            width: 400pt;
            height: 100%;
            right: -200pt;
        }
    }
}